/* @import './antd.scss'; */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: var(--font-inter);
}
 
h1 {
  font-family: var(--font-roboto-mono); 
}

html,
body {
  /* font-family:
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    Fira Sans,
    Droid Sans,
    Helvetica Neue,
    sans-serif;
  line-height: 1.6;
  font-size: 18px; */
  background-color: #09090B;
  color: #ffffff;
}

a {
  color: #0070f3;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  font-size: 2rem;
}
h2 {
  font-size: 1.8rem;
}
h3 {
  font-size: 1.2rem;
}
h4 {
  font-size: 1rem;
  padding-bottom: .5rem;
}
h5 {
  font-size: .9rem;
  font-weight: normal;
}
i {
  font-weight: normal;
}

.layout-wrapper {
  min-height: 100vh !important;
}


/* Default (Desktop) */
:root {
  --button-font-size: 166px;
  --button-padding-block: 12px;
  --button-padding-inline: 24px;
  --primary-color: #0000ff;
  --border-radius: 8px;
}

/* Mobile (max-width: 768px) */
@media (max-width: 768px) {
  :root {
    --button-font-size: 20px;
    --button-padding-block: 8px;
    --button-padding-inline: 16px;
  }
}

